.heading {
  position: relative;
  display: inline-block;
  font-size: 30px;
  margin-bottom: 30px;
}
.heading:after {
  content: '';
  position: absolute;
  height: 5px;
  width: 98%;
  right: 0;
  bottom: -8px;
  background-color: #EF476F;
}
.text-center {
  text-align: center;
}
#projects {
  height: 100vh;
  text-align: center;
}
#projects .heading {
  top: 35px;
}
#projects .project-container {
  max-width: 1350px;
  z-index: 0;
  margin: auto;
  position: relative;
  top: 150px;
}
#projects .project-container .project-tile {
  margin: 10px auto;
  cursor: pointer;
  width: 240px;
  height: 240px;
  position: relative;
}
@media (min-width: 767px) {
  #projects .project-container .project-tile {
    float: left;
    margin: 10px;
  }
}
#projects .project-container .project-tile:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  -webkit-transition: 0.5s background-color ease;
  -o-transition: 0.5s background-color ease;
  transition: 0.5s background-color ease;
}
#projects .project-container .project-tile:hover::after {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}
#projects .project-container .project-tile .project-copy {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
#projects .project-container .project-tile .project-copy h5 {
  color: #FFFFFF;
}
#projects .project-container .project-tile.pink {
  background-color: #EF476F;
}
#projects .project-container .project-tile.yellow {
  background-color: #FFD166;
}
#projects .project-container .project-tile.blue {
  background-color: #01BAEF;
}
#projects .project-container .project-tile.green {
  background-color: #00BFB2;
}
#projects .project-container .project-tile.purple {
  background-color: #88498F;
}



