.menu-container {
  position: relative;
  display: block;
  height: 50px;
  padding: 20px;
  z-index: 1;
}
.menu-container .pull-left {
  text-align: left;
}
.menu-container .pull-left h1 {
  display: inline;
}
.menu-container .pull-right {
  text-align: right;
}
.menu-container .pull-right .inline-list {
  list-style: none;
}
.menu-container .pull-right .inline-list li {
  display: inline;
  cursor: pointer;
  position: relative;
  margin: 0 15px;
}
.menu-container .pull-right .inline-list li.active-link {
  font-weight: 700;
}
.menu-container .pull-right .inline-list li:after {
  content: '';
  position: absolute;
  height: 5px;
  width: 0;
  left: 0;
  bottom: -5px;
  background-color: #EF476F;
  -webkit-transition: width 0.5s ease;
  -o-transition: width 0.5s ease;
  transition: width 0.5s ease;
}
.menu-container .pull-right .inline-list li:hover::after {
  width: 100%;
}



