.heading {
  position: relative;
  display: inline-block;
  font-size: 30px;
  margin-bottom: 30px;
}
.heading:after {
  content: '';
  position: absolute;
  height: 5px;
  width: 98%;
  right: 0;
  bottom: -8px;
  background-color: #EF476F;
}
.text-center {
  text-align: center;
}
.hero-container .panel-container {
  height: 95vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.hero-container .panel-container .panel-copy {
  padding: 90px;
  max-width: 1000px;
  margin: auto;
}
.hero-container .panel-container .panel-copy .panel-subheading {
  font-size: 18px;
}
.hero-container .arrow-container {
  text-align: center;
}
.hero-container .arrow-container .arrow {
  position: relative;
  bottom: 90px;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #EF476F;
  cursor: pointer;
}



