.heading {
  position: relative;
  display: inline-block;
  font-size: 30px;
  margin-bottom: 30px;
}
.heading:after {
  content: '';
  position: absolute;
  height: 5px;
  width: 98%;
  right: 0;
  bottom: -8px;
  background-color: #EF476F;
}
.text-center {
  text-align: center;
}
.viewer {
  background-color: transparent;
  width: 0;
  height: 0;
}
.viewer-main {
  position: fixed;
  background: white;
  height: 100%;
  top: 0;
  right: -900px;
  padding: 60px;
  width: 800px;
  -webkit-transition: all 500ms cubic-bezier(0.6, 0, 0.735, 0.045);
  /* older webkit */
  -webkit-transition: all 500ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  -o-transition: all 500ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transition: all 500ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  /* easeInBack */
  -webkit-transition-timing-function: cubic-bezier(0.6, 0, 0.735, 0.045);
  /* older webkit */
  -webkit-transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  -o-transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
  /* easeInBack */
  z-index: 1;
}
.viewer-main .viewer-body h1 {
  margin-bottom: 25px;
  font-size: 30px;
}
.viewer-main .viewer-body img {
  width: 400px;
}
.viewer-main .viewer-body .tech-container .tech-pill {
  background-color: #EF476F;
  border-radius: 19px;
  padding: 6px 12px;
  display: inline-block;
  margin: 10px 5px;
}
.viewer-main .viewer-body .tech-container .tech-pill span {
  color: #FFFFFF;
}
.viewer-main .close-viewer {
  cursor: pointer;
  text-align: right;
  font-size: 18px;
}
.viewer-main .close-viewer:hover {
  color: #EF476F;
}
.active {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: background-color 500ms cubic-bezier(0.42, 0, 1, 1);
  -o-transition: background-color 500ms cubic-bezier(0.42, 0, 1, 1);
  transition: background-color 500ms cubic-bezier(0.42, 0, 1, 1);
  /* ease-in */
  -webkit-transition-timing-function: cubic-bezier(0.42, 0, 1, 1);
  -o-transition-timing-function: cubic-bezier(0.42, 0, 1, 1);
  transition-timing-function: cubic-bezier(0.42, 0, 1, 1);
  /* ease-in */
  background-color: rgba(0, 0, 0, 0.6);
}
.active .viewer-main {
  right: 0;
}



